import request from "../utils/request"; // 获取code列表

export var getAllAction = function getAllAction(query) {
  return request({
    url: 'get_all_action',
    method: 'get',
    params: query
  });
}; // 删除action

export var deleteAction = function deleteAction(query) {
  return request({
    url: 'del_action',
    method: 'get',
    params: query
  });
}; // 编辑action

export var editAction = function editAction(query) {
  return request({
    url: 'edit_action',
    method: 'post',
    data: query
  });
}; // 新建action

export var addAction = function addAction(query) {
  return request({
    url: 'create_action',
    method: 'post',
    data: query
  });
};